import {
  mapMutations,
  mapGetters
} from 'vuex';

export default {
  layout: 'admin',
  components: {

  },
  data() {
    return {
      table: {
        max: 10
      },
       page: 1,
      titleDialog: 'Tambah User',
      tambahDialog: false,
      search: '',
      isUpdate: false,
      btnLoader: false,
      form: {
        nama: "",
        email: "",
        nip: "",
        unit_kerja: "",
        jabatan: "",
        no_hp: "",
        id_goverment: '',
        konfirmasi_password: '',
        organisasi: "",
        aktif: true,
        level: 2,
        foto: null
      },
    }
  },
  mounted() {
    this.$store.dispatch('user/getAll', {
      showall: 1
    });
    this.$store.dispatch('goverment/getPlains', {
      showall: 0
    });
    this.$store.dispatch('user/getUserSummary');
  },
  methods: {
    searchData(){
      this.$store.dispatch('user/getAll', {
        search: this.search
      });
    },
    edit(data) {
      let form = JSON.parse(JSON.stringify(data))
      let searchGov = form.goverment ? form.goverment.nama : ''
      // this.$store.dispatch('goverment/getAll', {
      //   search: searchGov,
      //   showall: 0
      // });
      this.tambahDialog = true
      this.titleDialog = 'Edit User'
      this.isUpdate = true
      // setTimeout(() => {
      this.form = form
      // }, 1000)
    },
    resetForm() {
      this.form = {
        nama: "",
        email: "",
        nip: "",
        unit_kerja: "",
        jabatan: "",
        no_hp: "",
        id_goverment: '',
        organisasi: "",
        aktif: true,
        level: 2,
        foto: null
      }
    },
    confirmation(type) {
      if (this.form.konfirmasi_password == this.form.password) {
        this.onSubmit(type)
      } else {
        // this.$notify.error({
        //   title: 'Konfirmasi Password',
        //   message: `Password Tidak Sama`
        // })
        this.openNotification('top-right', 'danger', `<i class='bx bxs-error-circle'></i>`, 'Konfirmasi Password', 'Password Tidak Sama')
      }
    },
    onSubmit(type = 'store') {
      this.btnLoader = true
      let url = "/user/store";
      if (type == 'update') {
        url = `/user/${this.form.id}/update`
      }

      this.$axios.post(url, this.form).then(resp => {
        if (resp.data.success) {
          this.$notify.success({
            title: 'Success',
            message: `Berhasil ${type == 'store' ? 'Menambah' : 'Mengubah'} User`
          })
          this.tambahDialog = false
          this.resetForm()
          this.$store.dispatch('user/getAll', {});
        }
      }).finally(() => {
        this.btnLoader = false
      }).catch(err => {
        let error = err.response.data.data
        if (error) {
          this.showErrorField(error)
        } else {
          this.$notify.error({
            title: 'Error',
            message: err.response.data.message
          })
        }
      })
    },
    deleteUser(id) {
      this.$swal({
        title: 'Perhatian!',
        text: "Apakah anda yakin ingin menghapus data ini?",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#3085d6',
        cancelButtonColor: '#d33',
        confirmButtonText: 'Ya',
        cancelButtonText: 'Batal'
      }).then((result) => {
        if (result.isConfirmed) {
          this.$axios.delete(`/user/${id}/delete`).then(resp => {
            if (resp.data.success) {
              this.$notify.success({
                title: 'Success',
                message: 'Berhasil Menghapus Data'
              })
              this.tambahDialog = false
              this.$store.dispatch('user/getAll', {
                defaultPage: true
              });
            }
          }).finally(() => {
            //
          }).catch(err => {
            this.$notify.error({
              title: 'Error',
              message: err.response.data.message
            })
          })
        }
      })
    },
    handleChangeSelect(data) {
      this.$store.dispatch('goverment/getAll', {
        search: data,
        showall: 0
      });
    }
  },
  computed: {
    ...mapGetters("user", [
      'getUsers',
      'getLoader',
      'getSummary'
    ]),
    ...mapGetters("goverment", [
      'getGovermentPlains',
    ])
  },
  watch: {
    getUsers(newValue, oldValue) {

    },
    getGovermentPlains(newValue) {
      // console.log(newValue)
    },
    search(newValue, oldValue) {
      // this.$store.dispatch('user/getAll', {
      //   search: newValue
      // });
    },
    page(newValue, oldValue) {
      this.$store.commit('user/setPage', newValue)
      this.$store.dispatch('user/getAll', {});
    }
  },
}

